<template>
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="180px">
      <!---->
      <Basic></Basic>

      <!---->
      <Spec></Spec>

      <!-- -->
      <Attr></Attr>

      <!-- -->
      <Ingredients></Ingredients>

      <!---->
      <Content></Content>

      <!---->
      <Buyset></Buyset>

      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Basic from './part/Basic.vue';
  import Attr from './part/Attr.vue';
  import Ingredients from './part/Ingredients.vue';
  import Spec from './part/Spec.vue';
  import Content from './part/Content.vue';
  import Buyset from './part/Buyset.vue';
  export default {
    components: {
      /**/
      Basic,
      /**/
      Spec,
      /* */
      Attr,
      /**/
      Ingredients,
      /**/
      Content,
      /**/
      Buyset
    },
    data() {
      return {
        /**/
        loading: false,
        /*form*/
        form: {
          model: {
            /**/
            product_name: '',
            /**/
            category_id: null,
            /**/
            image: [],
            /**/
            selling_point: '',
            /*,1020*/
            spec_type: 10,
            /*,2010*/
            deduct_stock_type: 20,
            /**/
            is_alone_grade: 0,
            sku: [{
              product_price: '',
              stock_num: '',
              product_weight: '',
              cost_price:0,
            }],
            product_attr: [],
            product_feed: [],
            /*  */
            min_buy: 1,
            /*  */
            product_unit: '',
            /* */
            /**/
            content: '',
            /**/
            product_status: 10,
            /**/
            sales_initial: 0,
            /*100*/
            product_sort: 100,
            /**/
            limit_num: 0,
            special_id: 0,
          
          },
          /**/
          category: [],
          feed: [],
          special: [],
          /**/
          delivery: [],
          /**/
          gradeList: [],
          /**/
          specData: null,
        }
      };
    },
    provide: function() {
      return {
        form: this.form
      }
    },
    created() {

      /**/
      this.getBaseData();

    },
    methods: {

      /**/
      getBaseData: function() {
        let self = this;
        PorductApi.storeGetBaseData({}, true)
          .then(res => {
            self.loading = false;
            Object.assign(self.form, res.data);
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /*JSON*/
      convertJson(list) {
        let obj = {};
        list.forEach(item => {
          obj[item.grade_id] = item.product_equity;
        });
        return JSON.stringify(obj);
      },

      /**/
      onSubmit: function() {
        let self = this;
        let params = self.form.model;
        self.$refs.form.validate(valid => {
          if (valid) {
            self.loading = true;
            params.alone_grade_equity = self.convertJson(self.form.gradeList);
            PorductApi.storeAddProduct({
                params: JSON.stringify(params)
              }, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/product/store/product/index');
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });
      },

      /**/
      Draft() {
        let self = this;
        self.form.model.product_status = 30;
        self.onSubmit();
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      }

    }
  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 100px;
  }
</style>



