<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-input size="small" v-model="searchForm.order_no" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.deliver_source" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option v-for="(item, index) in deliver_source" :key="index" :label="item.name" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker size="small" v-model="searchForm.create_time" type="daterange" value-format="yyyy-MM-dd"
              range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="success" @click="onExport"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData.data" border style="width: 100%"
          v-loading="loading">
          <el-table-column prop="order_no" label=""></el-table-column>
          <el-table-column prop="orders.pay_price" label=""></el-table-column>
          <el-table-column prop="price" label=""></el-table-column>
          <el-table-column prop="status_text" label=""></el-table-column>
          <el-table-column prop="deliver_status_text" label=""></el-table-column>
          <el-table-column prop="deliver_source_text" label=""></el-table-column>
          <el-table-column prop="linkman" label=""></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <el-table-column fixed="right" label="" width="120">
            <template slot-scope="scope">
              <el-button @click="detailClick(scope.row)" type="text" size="small" v-auth="'/takeout/deliver/detail'">
              </el-button>
              <el-button @click="cancelClick(scope.row)" v-if="scope.row.deliver_source!=10" type="text" size="small" v-auth="'/takeout/deliver/cancel'">
              </el-button>
              <el-button @click="verifyClick(scope.row)" v-if="scope.row.deliver_source==10&&scope.row.status==10" type="text" size="small" v-auth="'/takeout/deliver/verify'">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
          :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"></el-pagination>
      </div>
    </div>
    <Cancel v-if="open_edit" :open_edit="open_edit" :deliver_id="deliver_id" :order_no="order_no" @closeDialog="closeDialogFunc($event, 'edit')">
    </Cancel>
  </div>
</template>

<script>
  import TakeOutApi from '@/api/takeout.js';
  import Cancel from './dialog/cancel.vue';
  import qs from 'qs';
  export default {
    components: {
      Cancel,
    },
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        searchForm: {
          order_no: '',
          deliver_source: '',
          create_time: ''
        },
        /**/
        create_time: '',
        /**/
        open_edit: false,
        /**/
        order_no: 0,
        deliver_id:0,
        deliver_source:[]
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getData();
      },
      /**/
      getData() {
        let self = this;
        let Params = this.searchForm;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        self.loading = true;
        TakeOutApi.deliverList(Params, true)
          .then(res => {
            self.tableData.data = res.data.list.data;
            self.totalDataNumber = res.data.list.total;
            self.deliver_source=res.data.deliver_source;
            self.loading = false;
          })
          .catch(error => {});
      },
      /**/
      detailClick(row) {
        let self = this;
        let params = row.deliver_id;
        self.$router.push({
          path: '/takeout/deliver/detail',
          query: {
            deliver_id: params
          }
        });
      },
      /**/
      verifyClick(row) {
        let self = this;
        self.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.loading = true;
          TakeOutApi.verify({
              deliver_id: row.deliver_id
            }, true)
            .then(data => {
              self.loading = false;
              if (data.code == 1) {
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.getData();
              }
            })
            .catch(error => {
              self.loading = false;
            });

        }).catch(() => {
          self.loading = false;
        });
      },
      /**/
      cancelClick(item) {
        this.deliver_id = item.deliver_id;
        this.order_no = item.order_no;
        this.open_edit = true;
      },
      /**/
      onSubmit() {
        this.curPage = 1;
        this.tableData = [];
        this.getData();
      },
      onExport: function() {
        let baseUrl = window.location.protocol + '//' + window.location.host;
        window.location.href = baseUrl + '/index.php/shop/takeout.deliver/export?' + qs.stringify(this.searchForm);
      },
      /**/
      closeDialogFunc(e, f) {
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      closerefundDialogFunc(e, f) {
        if (f == 'edit') {
          this.open_refund = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
    }
  };
</script>
<style lang="scss">
  .product-info {
    padding: 10px 0;
    border-top: 1px solid #eeeeee;
  }

  .order-code .state-text {
    padding: 2px 4px;
    border-radius: 4px;
    background: #808080;
    color: #ffffff;
  }

  .order-code .state-text-red {
    background: red;
  }

  .table-wrap .product-info:first-of-type {
    border-top: none;
  }

  .table-wrap .el-table__body tbody .el-table__row:nth-child(odd) {
    background: #f5f7fa;
  }
</style>



