<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="pb50" v-loading="loading">
    <div class="product-content">
      <!---->
      <div class="common-form"></div>
      <div class="table-wrap">
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.order_no }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.user.nickName }}
              <span>ID({{ detail.user.user_id }})</span>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.order_price }}
            </div>
          </el-col>
          <el-col :span="5" v-if="detail.express_price > 0">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.express_price }}
            </div>
          </el-col>
          <el-col :span="5" v-if="detail.bag_price > 0">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.bag_price }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16" v-if="detail.coupon_money > 0">
              <span class="gray9"> ()</span>
              {{ detail.coupon_money }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16" v-if="detail.points_money > 0">
              <span class="gray9"> ()</span>
              {{ detail.points_money }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16" v-if="detail.fullreduce_money > 0">
              <span class="gray9"> ()</span>
              {{ detail.fullreduce_money }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.pay_price }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.pay_type.text }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.delivery_type.text }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9" v-if="detail.delivery_type.value==10"></span>
              <span class="gray9" v-if="detail.delivery_type.value==20"></span>
              {{ detail.mealtime}}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.order_status.text }}
            </div>
          </el-col>
        </el-row>
      </div>
      <!---->
      <div class="common-form mt16"></div>
      <div class="table-wrap">
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.supplier.name }}
            </div>
          </el-col>
        </el-row>
      </div>
      <!---->
      <div class="common-form mt16"></div>
      <div class="table-wrap">
        <el-table size="small" :data="detail.product" border style="width: 100%">
          <el-table-column prop="product_name" label="" width="400">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image.file_path" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                  <div class="gray9" v-if="scope.row.product_attr!=''">{{scope.row.product_attr}}</div>
                  <div class="orange" v-if="scope.row.refund">
                    {{ scope.row.refund.type.text }}-{{ scope.row.refund.status.text }}
                  </div>
                  <div class="price">
                    <span :class="{'text-d-line':scope.row.is_user_grade==1,'gray6':scope.row.is_user_grade!=1}">
                      {{ scope.row.product_price }}</span>
                    <span class="ml10" v-if="scope.row.is_user_grade==1">
                       {{ scope.row.grade_product_price }}
                    </span>
                  </div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="total_num" label="">
            <template slot-scope="scope">
              <p>x {{ scope.row.total_num }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="total_price" label="()">
            <template slot-scope="scope">
              <p> {{ scope.row.total_price }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div v-if="detail.delivery_type.value == 10">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.name }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.phone }}
              </div>
            </el-col>
            <el-col :span="9">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.detail }}{{ detail.address.address }}
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="25">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.buyer_remark }}
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <!---->
      <template v-if="detail.delivery_type.value == 20">
        <div class="common-form mt16"></div>
        <div class="table-wrap" v-if="detail.extract">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract.phone }}
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="25">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.buyer_remark }}
              </div>
            </el-col>
          </el-row>
        </div>
      </template>

      <!---->
      <template v-if="detail.delivery_type.value == 30">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.user.mobile }}
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="25">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.buyer_remark }}
              </div>
            </el-col>
          </el-row>
        </div>
      </template>

      <!---->
      <div class="table-wrap" v-if="detail.pay_status.value == 20">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_price }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_type.text }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.transaction_id }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_status.text }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_time }}
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <!---->
      <div v-if="detail.delivery_type.value == 10">
        <div>
          <div class="common-form mt16"></div>
          <div class="table-wrap">
            <el-row>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.deliver_source_text }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.deliver_status_text }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.distance }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.linkman }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.phone }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.price }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ deliver.deliver_time }}
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>

      <!---->
      <div class="table-wrap" v-if="detail.order_status.value == 20 && detail.cancel_remark != ''">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.cancel_remark }}
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div class="common-button-wrapper">
      <el-button size="small" type="info" @click="cancelFunc"></el-button>
      <!---->
      <template v-if="deliver.status == 10">
        <el-button size="small" type="primary" @click="onSubmit">
        </el-button>
      </template>
    </div>
  </div>
</template>

<script>
  import TakeOutApi from '@/api/takeout.js';
  import {
    deepClone,

  } from '@/utils/base.js';
  export default {
    components: {
    },
    data() {
      return {
        active: 0,
        /**/
        loading: true,
        /**/
        detail: {
          pay_status: [],
          pay_type: [],
          delivery_type: [],
          user: {},
          address: [],
          product: [],
          order_status: [],
          extract: [],
          extract_store: [],
          express: [],
          delivery_status: [],
          extractClerk: [],
          supplier: {
            name: ''
          }
        },
        /**/
        open_add: false,
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        order: {},
        delivery_type: 0,
        addressData: {
          name: '',
          phone: '',
          detail: '',
          address: ''
        },
        deliver:{}
      };
    },
    created() {
      /**/
      this.getParams();
    },
    methods: {
      next() {
        if (this.active++ > 4) this.active = 0;
      },
      /**/
      getParams() {
        let self = this;
        // 
        const params = this.$route.query.deliver_id;
        TakeOutApi.detail({
              deliver_id: params
            },
            true
          )
          .then(data => {
            self.loading = false;
            self.detail = data.data.detail;
            self.deliver = data.data.deliver;
            if (self.detail.address != null) {
              self.addressData = self.detail.address;
            }
            console.log(self.addressData)
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /**/
      onSubmit() {
        let self = this;
        let deliver_id = this.$route.query.deliver_id;
        TakeOutApi.verify({
              deliver_id: deliver_id
            },
            true
          )
          .then(data => {
            self.loading = false;
            self.$message({
              message: '',
              type: 'success'
            });
            self.getParams();
          })
          .catch(error => {
            self.loading = false;
          });
      },
      /**/
      cancelFunc() {
        this.$router.back(-1);
      },
    }
  };
</script>
<style></style>



