<template>
  <!--
    	wangxw
    	2019-05-05
    	
    -->
  <div class="d-s-s">
    <!---->
    <div class="model-container">
      <div class="img-box">
        <el-image v-if="formData.category_style==10" :src="category_10" fit="fill"></el-image>
        <el-image v-if="formData.category_style==20" :src="category_20" fit="fill"></el-image>
        <el-image v-if="formData.category_style==30" :src="category_30" fit="fill"></el-image>
      </div>
    </div>
    <!---->
    <div class="param-container flex-1">
      <div class="common-form"><span></span></div>
      <el-form size="small" :model="formData" label-width="100px">
        <el-form-item label="">
          <el-radio-group v-model="formData.category_style">
            <el-radio :label="10">()</el-radio>
            <el-radio :label="20">()</el-radio>
            <el-radio :label="30"></el-radio>
          </el-radio-group>
          <p class="gray9">()710*300240*240</p>
        </el-form-item>
        <el-form-item label=""><el-input v-model="formData.share_title"></el-input></el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="common-button-wrapper"><el-button size="small" type="primary" @click="Submit()"></el-button></div>
  </div>
</template>

<script>
  import PageApi from '@/api/page.js';
  import category_10 from '@/assets/img/category_10.jpg';
  import category_20 from '@/assets/img/category_20.jpg';
  import category_30 from '@/assets/img/category_30.jpg';
export default {
  data() {
    return {
      /**/
      formData: {
        category_style: null
      },
      /**/
      category_10:category_10,
      category_20:category_20,
      category_30:category_30,
    };
  },
  created() {

    this.getData();

  },
  methods: {

    /**/
    getData() {
      let self = this;
      PageApi.getCategory({}, true).then(res => {
        self.formData=res.data.model;
      }).catch(error => {
        self.loading = false;
      });
    },

    /**/
    Submit() {
      let self = this;
      self.loading = true;
      let Params = self.formData;
      PageApi.postCategory(Params,true)
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
          self.getData();
        })
        .catch(error => {
          self.loading = false;
        });
    }
  }
};
</script>

<style scoped="scoped">
.model-container {
  width: 300px;
  height: calc(100vh - 150px);
  margin-right: 30px;
}
.model-container img{width: 100%;}
.model-container .img-box{ box-shadow: 0 0 16px 0 rgba(0,0,0,.1);}
.param-container {
  padding: 20px;
  height: calc(100vh - 150px);
  border: 1px solid #cccccc;
}
</style>



