<template>
  <!--
    	luoyiming
    	2019-10-26
    	--/
    -->
  <div>
    <!---->
    <div class="common-form mt50"></div>
    <!---->
    <div>
      <div class="mt16">
        <!-- <el-form-item label=""> -->
        <div class="p-0-30 mb18">
          <div class="d-s-c ">: <div class="blue ml30" @click="addAttr">+</div>
          </div>
        </div>

        <!---->
        <div class="mb18 p-0-30" v-for="(item,index) in form.model.attr_many.attr_list" :key="index"
          v-if="form.model.attr_many.attr_list.length>0">
          <div class="d-c-c mb16">
            <div style="width: 100px;"><span class="red">*</span></div>
            <div class="flex-1"><span class="red">*</span>()</div>
          </div>
          <div class="d-c-c">
            <div style="width: 100px;">
              <el-input size="medium" v-model="item.name" placeholder=":"></el-input>
            </div>
            <div class="flex-1 d-s-c">
              <div v-for="(aitem,aindex) in 8" :key="aindex" class="ml20">
                <el-input style="width: 100px;" size="medium" v-model="item.attrs[aindex]" placeholder=""></el-input>
              </div>
            </div>
          </div>
        </div>
        <!-- </el-form-item> -->
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    inject: ['form'],
    methods: {
      addAttr() {
        this.form.model.attr_many.attr_list.push({
          name: '',
          attrs: []
        })
      }
    }
  };
</script>

<style>

</style>



