<template>
  <!--
    	luoyiming
    	2019-10-26
    	--/
    -->
  <div>
    <!---->
    <div class="common-form mt50"></div>
    <!---->
    <div>
      <div class="mt16">
        <!-- <el-form-item label=""> -->
        <div class="p-0-30 mb18">
          <div class="d-s-c ">: <div class="blue ml30" @click="addIngredients">+</div>
          </div>
        </div>

        <!---->
        <div class="p-0-30 mb18" v-for="(item,index) in form.model.ingredients_list" :key="index"
          v-if="form.model.ingredients_list.length>0">
          <div class="d-c-c mb16">
            <div style="width: 100px;"><span class="red">*</span></div>
            <div class="flex-1"><span class="red">*</span></div>
          </div>
          <div class="d-s-c">
            <div style="width: 100px;">
              <el-input size="medium" v-model="item.name" placeholder=":"></el-input>
            </div>
            <div class="d-s-c ml20">
                <el-input type="number" style="width: 100px;" size="medium" v-model="item.price" placeholder=""></el-input> <span class="ml10"></span>
            </div>
          </div>
        </div>
        <!-- </el-form-item> -->
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    inject: ['form'],
    methods: {
      addIngredients() {
        this.form.model.ingredients_list.push({
          name: '',
          price: ''
        })
      }
    }
  };
</script>

<style>

</style>



