<template>
  <!--
      	
      -->
  <div class="common-seach-wrap">
    <!---->
    <tables v-if="activeName=='tables'"></tables>
    <!---->
    <tablearea v-if="activeName=='tablearea'"></tablearea>
    <!---->
    <tabletype v-if="activeName=='tabletype'"></tabletype>
  </div>
</template>
<script>
  import bus from '@/utils/eventBus.js';
  import tables from './table/index.vue';
  import tablearea from './area/index.vue';
  import tabletype from './type/index.vue';
  export default {
    components: {
      tables,
      tablearea,
      tabletype,
    },
    data() {
      return {
        /**/
        loading: true,
        form: {},
        /**/
        param: {},
        /**/
        activeName: 'tables',
        /**/
        sourceList: [{
            key: 'tables',
            value: '',
            path: '/store/table/table/index'
          },
          {
           key: 'tablearea',
           value: '',
           path: '/store/table/area/index'
          },
          {
           key: 'tabletype',
           value: '',
           path: '/store/table/type/index'
          },

        ],
        /**/
        tabList: [],
      };
    },
    watch:{

      //
      $route(to, from) {
        this.init();
      }
    },
    created() {
      this.init()

    },
    beforeDestroy() {
     //
     bus.$emit('tabData', { active: null, tab_type:'tablemanage',list: [] });
     bus.$off('activeValue');
    },
    methods: {
      /**/
      init(){
        this.tabList=this.authFilter();

        if(this.tabList.length>0){
          this.activeName=this.tabList[0].key;
        }

        if (this.$route.query.type != null) {
          this.activeName = this.$route.query.type;
        }

        /**/
        bus.$on('activeValue', res => {
          if (this.is_third_param) {
            this.param.user_id = '';
            this.is_third_param = false;
          }
          this.activeName = res;
        });

        //
        let params = {
          active: this.activeName,
          list: this.tabList,
          tab_type:'tablemanage'
        };
        bus.$emit('tabData', params);
      },
      /**/
      authFilter() {
        let list = [];
        for (let i = 0; i < this.sourceList.length; i++) {
          let item = this.sourceList[i];
          if (this.$filter.isAuth(item.path)) {
            list.push(item);
          }
        }
        return list;
      },

    }
  };
</script>

<style>
  .operation-wrap {
    height: 124px;
    border-radius: 8px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 30px 30px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    overflow: hidden;
    background: #909399;
    background-size: 100% 100%;
    color: #fff;
  }
</style>



