<template>
  <!--
    	luoyiming
    	2019-10-26
    	--/
    -->
  <div>
    <!---->
    <div class="common-form mt50"></div>
    <!---->
    <div>
      <div class="mt16">
        <!-- <el-form-item label=""> -->
        <div class="p-0-30 mb18">
          <div class="d-s-c ">: <div class="blue ml30" @click="addIngredients">+</div>
          </div>
        </div>

        <!---->
        <div class="p-0-30 mb18" v-for="(item,index) in form.model.product_feed" :key="index"
          v-if="form.model.product_feed.length>0">
          <div class="d-c-c mb16">
            <div style="width: 100px;"><span class="red">*</span></div>
            <div class="flex-1"><span class="red">*</span></div>
          </div>
          <div class="d-s-c">
            <div style="width: 100px;">
              <el-autocomplete class="inline-input" v-model="item.feed_name" placeholder=":"
                :fetch-suggestions="querySearch"></el-autocomplete>
            </div>
            <div class="d-s-c ml20">
              <el-input type="number" style="width: 100px;" size="medium" v-model="item.price" placeholder="">
              </el-input> <span class="ml10"></span>
            </div>
          </div>
        </div>
        <!-- </el-form-item> -->
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        restaurants: [],
        formData: {
          feed: []
        },
      }
    },
    inject: {
      form: {
        default: () => {}
      },
    },
    methods: {
      addIngredients() {
        this.form.model.product_feed.push({
          feed_name: '',
          price: ''
        })
      },
      querySearch(queryString, cb) {
        let self = this;
        if (self.restaurants.length == 0) {
          self.form.feed.forEach((item, index) => {
            self.restaurants.push({
              value: item.feed_name
            })
          })
        }

        var restaurants = self.restaurants;
        var results = queryString ? restaurants.filter(self.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
    }
  };
</script>

<style>

</style>



