<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-input size="small" v-model="searchForm.order_no" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.style_id" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option v-for="(item, index) in exStyle" :key="index" :label="item.name" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker size="small" v-model="searchForm.create_time" type="daterange" value-format="yyyy-MM-dd"
              range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="success" @click="onExport"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="" name="all"></el-tab-pane>
          <el-tab-pane :label="''" name="payment">
            <span slot="label"> <el-tag size="mini">{{order_count.payment}}</el-tag></span>
          </el-tab-pane>
          <el-tab-pane :label="''" name="process">
            <span slot="label"> <el-tag size="mini">{{order_count.process}}</el-tag></span>
          </el-tab-pane>
          <el-tab-pane :label="''" name="complete">
            <span slot="label"> <el-tag size="mini">{{order_count.complete}}</el-tag></span>
          </el-tab-pane>
        </el-tabs>
        <el-table size="small" :data="tableData.data" :span-method="arraySpanMethod" border style="width: 100%"
          v-loading="loading">
          <el-table-column prop="order_no" label="" width="400">
            <template slot-scope="scope">
              <div class="order-code" v-if="scope.row.is_top_row">
                <span class="state-text"
                  :class="{'state-text-red':scope.row.order_source != 10}">{{scope.row.order_source_text}}</span>
                <span class="c_main">{{ scope.row.order_no }}</span>
                <span class="pl16">{{ scope.row.create_time }}</span>
                <!---->
                <span class="pl16" v-if="scope.row.order_status == 21">
                  <el-tag effect="dark" size="mini"></el-tag>
                </span>
              </div>
              <template v-else>
                <div class="product-info" v-for="(item, index) in scope.row.product" :key="index">
                  <div class="pic"><img v-img-url="item.image.file_path" alt="" /></div>
                  <div class="info">
                    <div class="name gray3 product-name">
                      <span>{{ item.product_name }}</span>
                    </div>
                    <div class="gray9" v-if="item.product_attr">{{ item.product_attr }}</div>
                    <div class="orange" v-if="item.refund">{{ item.refund.type.text }}-{{ item.refund.status.text }}
                    </div>
                  </div>
                  <div class="d-c-c d-c">
                    <div class="orange"> {{ item.product_price }}</div>
                    <div class="gray3">x{{ item.total_num }}</div>
                  </div>
                </div>
              </template>
            </template>
          </el-table-column>
          <el-table-column prop="pay_price" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <div class="orange">{{ scope.row.pay_price }}</div>
              <p class="gray9">({{ scope.row.express_price }})</p>
              <p class="gray9">({{ scope.row.bag_price }})</p>
            </template>
          </el-table-column>
          <el-table-column prop="" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <div>{{ scope.row.user.nickName }}</div>
              <div class="gray9">ID({{ scope.row.user.user_id }})</div>
            </template>
          </el-table-column>
          <el-table-column prop="supplier.name" label=""></el-table-column>
          <el-table-column prop="state_text" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              {{ scope.row.state_text }}
            </template>
          </el-table-column>
          <el-table-column prop="pay_type.text" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <span class="gray9">{{ scope.row.pay_type.text }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="delivery_type.text" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <span class="green">{{ scope.row.delivery_type.text }}</span>
              <span v-if="scope.row.delivery_type.value == 30">:{{scope.row.user.mobile}}</span>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="" width="200">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <el-button @click="addClick(scope.row)" type="text" size="small" v-auth="'/takeout/order/detail'">
              </el-button>
              <el-button v-if="scope.row.order_status.value==10&&scope.row.pay_status.value==20"
                @click="refundClick(scope.row)" type="text" size="small" v-auth="'/takeout/Operate/refund'">
              </el-button>
              <el-button v-if="scope.row.order_status.value==10&&scope.row.delivery_status.value==10"
                @click="cancelClick(scope.row)" type="text" size="small" v-auth="'/takeout/operate/confirmCancel'">
              </el-button>
              <el-button
                v-if="scope.row.order_status.value==10&&scope.row.delivery_type.value==20&&scope.row.pay_status.value==20"
                @click="verifyClick(scope.row)" type="text" size="small" v-auth="'/takeout/operate/extract'">
              </el-button>
              <el-button
                v-if="scope.row.order_status.value==10&&scope.row.delivery_type.value==10&&scope.row.pay_status.value==20&&scope.row.delivery_status.value==20"
                @click="verifyClick(scope.row)" type="text" size="small" v-auth="'/takeout/operate/extract'">
              </el-button>
              <el-button @click="senDada(scope.row)"
                v-if="scope.row.pay_status.value==20&&scope.row.deliver_status==0&&scope.row.order_status.value==10&&scope.row.delivery_type.value==10"
                type="text" size="small" v-auth="'/takeout/operate/sendOrder'">{{deliver_name}}
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
          :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"></el-pagination>
      </div>
    </div>
    <Cancel v-if="open_edit" :open_edit="open_edit" :order_no="order_no" @closeDialog="closeDialogFunc($event, 'edit')">
    </Cancel>
    <!---->
    <refund v-if="open_refund" :open_edit="open_refund" :order_no="order_no"
      @closeDialog="closerefundDialogFunc($event, 'edit')">
    </refund>
  </div>
</template>

<script>
  import OrderApi from '@/api/order.js';
  import Cancel from './dialog/cancel.vue';
  import refund from './dialog/refund.vue';
  import qs from 'qs';
  export default {
    components: {
      Cancel,
      refund,
    },
    data() {
      return {
        /**/
        activeName: 'all',
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        searchForm: {
          order_no: '',
          style_id: '',
          create_time: ''
        },
        /**/
        exStyle: [],
        /**/
        shopList: [],
        /**/
        create_time: '',
        /**/
        order_count: {
          payment: 0,
          delivery: 0,
          received: 0
        },
        /**/
        open_edit: false,
        open_refund: false,
        /**/
        order_no: 0,
        deliver_name: ''
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      arraySpanMethod(row) {
        if (row.rowIndex % 2 == 0) {
          if (row.columnIndex === 0) {
            return [1, 8];
          }
        }
      },
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getData();
      },

      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.getData();
      },

      /**/
      getData() {
        let self = this;
        let Params = this.searchForm;
        Params.dataType = self.activeName;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        self.loading = true;
        OrderApi.takeOrderlist(Params, true)
          .then(res => {
            let list = [];
            for (let i = 0; i < res.data.list.data.length; i++) {
              let item = res.data.list.data[i];
              let topitem = {
                order_no: item.order_no,
                create_time: item.create_time,
                order_source: item.order_source,
                order_source_text: item.order_source_text,
                is_top_row: true,
                order_status: item.order_status.value,
              };
              list.push(topitem);
              list.push(item);
            }
            self.tableData.data = list;
            self.deliver_name = res.data.deliver_name;
            self.totalDataNumber = res.data.list.total;
            self.exStyle = res.data.ex_style;
            self.order_count = res.data.order_count.order_count;
            self.loading = false;
          })
          .catch(error => {});
      },

      /**/
      addClick(row) {
        let self = this;
        let params = row.order_id;
        self.$router.push({
          path: '/takeout/order/detail',
          query: {
            order_id: params
          }
        });
      },
      /**/
      verifyClick(row) {
        let self = this;
        let extract_form = {};
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          extract_form.order_id = row.order_id;
          OrderApi.takeExtract(extract_form,
              true
            )
            .then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.getData();
            })
            .catch(error => {
              self.loading = false;
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      },
      senDada(row) {
        let self = this;
        let extract_form = {};
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          extract_form.order_id = row.order_id;
          OrderApi.sendDada(extract_form,
              true
            )
            .then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.getData();
            })
            .catch(error => {
              self.loading = false;
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      },
      /**/
      onSubmit() {
        this.curPage = 1;
        this.tableData = [];
        this.getData();
      },
      onExport: function() {
        let baseUrl = window.location.protocol + '//' + window.location.host;
        window.location.href = baseUrl + '/index.php/shop/takeout.operate/export?' + qs.stringify(this.searchForm);
      },
      /**/
      cancelClick(item) {
        this.order_no = item.order_id;
        this.open_edit = true;
      },
      refundClick(item) {
        this.order_no = item.order_id;
        this.open_refund = true;
      },
      /**/
      closeDialogFunc(e, f) {
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      closerefundDialogFunc(e, f) {
        if (f == 'edit') {
          this.open_refund = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
    }
  };
</script>
<style lang="scss">
  .product-info {
    padding: 10px 0;
    border-top: 1px solid #eeeeee;
  }

  .order-code .state-text {
    padding: 2px 4px;
    border-radius: 4px;
    background: #808080;
    color: #ffffff;
  }

  .order-code .state-text-red {
    background: red;
  }

  .table-wrap .product-info:first-of-type {
    border-top: none;
  }

  .table-wrap .el-table__body tbody .el-table__row:nth-child(odd) {
    background: #f5f7fa;
  }
</style>



