<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div>
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="user">
      </el-tab-pane>
      <el-tab-pane label="" name="shop">
      </el-tab-pane>
      <el-tab-pane label="" name="supplier">
      </el-tab-pane>
      <el-tab-pane label="" name="mobile">
      </el-tab-pane>
    </el-tabs>
    
    <!---->
    <Message v-if="activeName=='user'" :message_to="10"></Message>
    <!---->
    <Message v-if="activeName=='shop'" :message_to="20"></Message>
    <!---->
    <Message v-if="activeName=='supplier'" :message_to="30"></Message>
    <!---->
    <GetPhone v-if="activeName=='mobile'"></GetPhone>

  </div>
</template>
<script>
  import Message from './Message';
  import GetPhone from './getphone.vue'
  export default {
    components: {
      /**/
      Message,
      /**/
      GetPhone
    },
    data() {
      return {
        /**/
        message_to: 10,
        /**/
        activeName: 'user'
      };
    },
    created() {
      if(this.$route.query.type!=null){
        this.activeName=this.$route.query.type;
      }

    },
    methods: {

    }
  };
</script>



