<template>
  <!--
    	--/
    -->
  <div>
    <!---->
    <div class="common-form mt50"></div>
    <!---->
    <div>
      <div class="mt16">
        <!-- <el-form-item label=""> -->
        <div class="p-0-30 mb18">
          <div class="d-s-c ">: <div class="blue ml30" @click="addAttr">+</div>
          </div>
        </div>

        <!---->
        <div class="mb18 p-0-30" v-for="(item,index) in form.model.product_attr" :key="index"
          v-if="form.model.product_attr.length>0">
          <div class="d-c-c mb16">
            <div style="width: 100px;"><span class="red">*</span></div>
            <div class="flex-1"><span class="red">*</span>()</div>
          </div>
          <div class="d-c-c">
            <div style="width: 100px;">
              <el-autocomplete class="inline-input" size="medium" v-model="item.attribute_name"
                :fetch-suggestions="querySearch" placeholder=":"></el-autocomplete>
            </div>
            <div class="flex-1 d-s-c">
              <div v-for="(aitem,aindex) in 8" :key="aindex" class="ml20">
                <el-input style="width: 100px;" size="medium" v-model="item.attribute_value[aindex]" placeholder="">
                </el-input>
              </div>
            </div>
          </div>
        </div>
        <!-- </el-form-item> -->
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        restaurants: [],
        formData: {},
      }
    },
    inject: ['form'],
    methods: {
      addAttr() {
        console.log(this.form.model.product_attr)
        this.form.model.product_attr.push({
          attribute_name: '',
          attribute_value: []
        })
      },
      querySearch(queryString, cb) {
        let self = this;
        if(self.restaurants.length==0){
          self.form.attribute.forEach((item, index) => {
            self.restaurants.push({
              value: item.attribute_name
            })
          })
        }
        
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
    }
  };
</script>

<style>

</style>



