<template>
  <!--
      	-
      -->
  <div class="common-seach-wrap">
    <!---->
    <product v-if="activeName=='product'"></product>
    <!---->
    <category v-if="activeName=='category'"></category>
  </div>
</template>
<script>
  import bus from '@/utils/eventBus.js';
  import category from './category/index.vue';
  import product from './product/index.vue';
  export default {
    components: {
      category,
      product,
    },
    data() {
      return {
        /**/
        loading: true,
        form: {},
        /**/
        param: {},
        /**/
        activeName: 'product',
        /**/
      sourceList: [
        {
            key: 'product',
            value: '',
            path:'/product/takeaway/product/index'
        },
        {
            key: 'category',
            value: '',
            path:'/product/takeaway/category/index'
        },

      ],
        /**/
        tabList:[],
      };
    },
    watch:{
    
      //
      $route(to, from) {
        this.init();
      }
    },
    created() {
      this.init()
    
    },
    beforeDestroy() {
     //
     bus.$emit('tabData', { active: null, tab_type:'storeproduct',list: [] });
     bus.$off('activeValue');
    },
    methods: {
      /**/
      init(){
        this.tabList=this.authFilter();
    
        if(this.tabList.length>0){
          this.activeName=this.tabList[0].key;
        }
    
        if (this.$route.query.type != null) {
          this.activeName = this.$route.query.type;
        }
    
        /**/
        bus.$on('activeValue', res => {
          if (this.is_third_param) {
            this.param.user_id = '';
            this.is_third_param = false;
          }
          this.activeName = res;
        });
    
        //
        let params = {
          active: this.activeName,
          list: this.tabList,
          tab_type:'storeproduct'
        };
        bus.$emit('tabData', params);
      },
      /**/
      authFilter() {
        let list = [];
        for (let i = 0; i < this.sourceList.length; i++) {
          let item = this.sourceList[i];
          if (this.$filter.isAuth(item.path)) {
            list.push(item);
          }
        }
        return list;
      },
    
    }
  };
</script>

<style>
  .operation-wrap {
    height: 124px;
    border-radius: 8px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 30px 30px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    overflow: hidden;
    background: #909399;
    background-size: 100% 100%;
    color: #fff;
  }
</style>



