<template>
  <!--
    	--
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false"
    :close-on-press-escape="false">
    <el-form size="small" :model="form" :rules="formRules" ref="form">
      <el-form-item label="" prop="sort" :label-width="formLabelWidth">
        <el-input v-model.number="form.sort" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="spec_name" :label-width="formLabelWidth">
        <el-input v-model="form.spec_name" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="submit" :loading="loading"> </el-button>
    </div>
  </el-dialog>

</template>

<script>
  import PorductApi from '@/api/product.js';
  import Upload from '@/components/file/Upload';
  export default {
    components: {
      Upload
    },
    data() {
      return {
        form: {
          spec_name: '',
          sort: 100,
        },
        formRules: {
          spec_name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          sort: [{
            required: true,
            message: ''
          }, {
            type: 'number',
            message: ''
          }]
        },
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false,
        loading: false,
        /**/
        isupload: false,
      };
    },
    props: ['open_add', 'addform'],
    created() {
      this.dialogVisible = this.open_add;
    },
    methods: {
      addvalue() {
        this.form.attribute_value.push('')
      },
      deleteattr(i) {
        this.form.attribute_value.splice(i, 1)
      },
      submit() {
        let self = this;
        let params = self.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            PorductApi.addSpec(params).then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.dialogFormVisible(true);
            }).catch(error => {
              self.loading = false;
            });
          }
        });
      },
      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          })
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          })
        }
      },

    }
  };
</script>

<style>
  .img {
    margin-top: 10px;
  }
</style>



