<template>
  <!--
      	luoyiming
      	2020-06-01
      	---
      -->
  <div>
    <el-dialog title="" width="35%" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false">
      <div class="d-a-c">
        <el-button @click="qrcodeClick" type="primary" plain></el-button>
        <!-- <el-button type="primary" plain>H5</el-button> -->
      </div>
      <div slot="footer" class="dialog-footer">
      </div>
    </el-dialog>
  </div>
</template>

<script>
import SupplierApi from '@/api/supplier.js';
import qs from 'qs';
export default {
  data() {
    return {
      status: '',
      reject_reason: '',
      /**/
      formLabelWidth: '120px',
      /**/
      dialogVisible: false,
      loading:false
    };
  },
  props: ['open','code_id'],
  watch:{
    open:function(n,o){
       this.dialogVisible = this.open;
    }
  },
  created() {

  },
  methods: {
    qrcodeClick(){
      let baseUrl = window.location.protocol + '//' + window.location.host;
      let params = {
        shop_supplier_id: this.code_id,
        source: 'wx'
      };
      window.location.href = baseUrl + '/index.php/shop/supplier.supplier/qrcode?' + qs.stringify(params);
    },
    /**/
    cashSubmit() {
      let self = this;
      let params = this.form;
      SupplierApi.cashSubmit(params, true)
        .then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          self.dialogFormVisible(true);
        })
        .catch(error => {});
    },

    /**/
    dialogFormVisible(e) {
      if (e) {
        this.$emit('close', true);
      } else {
        this.$emit('close', false);
      }
    }
  }
};
</script>

<style></style>



