<template>
  <!--
      
  -->
  <div class="product-list">
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" icon="el-icon-plus" v-auth="'/product/expand/unit/add'" @click="addClick">
        </el-button>
      <el-button size="small"  v-auth="'/product/expand/attr/delete'"
        @click="deleteBatch"></el-button>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading"
          @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="45"></el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="unit_id" label="ID"></el-table-column>
          <el-table-column prop="unit_name" label="" width="400px"></el-table-column>
          <el-table-column fixed="right" label="" width="120">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/expand/unit/edit'">
              </el-button>
              <el-button @click="deleteClick(scope.row.unit_id)" type="text" size="small"
                v-auth="'/product/expand/unit/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <div class="pagination">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
        :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper"
        :total="totalDataNumber"></el-pagination>
    </div>
    <!---->
    <Add v-if="open_add" :open_add="open_add" :addform="model" @closeDialog="closeDialogFunc($event, 'add')"></Add>
    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :editform="model" @closeDialog="closeDialogFunc($event, 'edit')">
    </Edit>

  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Add from './add.vue';
  import Edit from './edit.vue';
  export default {
    components: {
      Add,
      Edit
    },
    data() {
      return {
        /**/
        activeName: 'sell',
        /**/
        activeIndex: '0',
        /**/
        loading: true,
        /**/
        pageSize: 10,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        model: {

        },
        open_edit: false,
        open_add: false,
        /**/
        tableData: [],
        multipleSelection: []
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.loading = true;
        self.curPage = val;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.pageSize = val;
        this.getData();
      },

      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.getData();
      },

      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        self.loading = true;
        PorductApi.UnitList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total;
          })
          .catch(error => {
            self.loading = false;
          });
      },
      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      addClick() {
        this.open_add = true;
      },
      deleteClick(id) {
        let self = this;
        self.$confirm('?', '', {
            type: 'warning'
          })
          .then(() => {
            PorductApi.deleteUnit({
              unit_id: id
            }).then(data => {
              self.$message({
                message: '',
                type: 'success'
              });
              self.getData();
            });
          });
      },
      deleteBatch() {
        let self = this;
        console.log(this.multipleSelection);
        let arr = [];
        this.multipleSelection.forEach((item, index) => {
          arr.push(item.unit_id);
        })
        let unit_id = arr.join(',');
        self.$confirm('?', '', {
            type: 'warning'
          })
          .then(() => {
            PorductApi.deleteUnit({
              unit_id: unit_id
            }).then(data => {
              self.$message({
                message: '',
                type: 'success'
              });
              self.getData();
            });
          });
      },
      handleSelectionChange(e) {
        this.multipleSelection = e;
      },
      /**/
      editClick(row) {
        this.model = row;
        this.open_edit = true;
      },
      /* */
      undercarriage(row, state) {
        let self = this;
        let war = "";
        let war_ = '';
        if (state == 20) {
          war = "",
            war_ = ''
        } else if (state == 10) {
          war = "",
            war_ = ''
        }
        self.$confirm("" + war + "?", '', {
            type: 'warning'
          })
          .then(() => {
            PorductApi.auditProduct({
              product_id: row.product_id,
              state
            }).then(data => {
              self.$message({
                message: war_ + '',
                type: 'success'
              });
              self.getData();
            });
          });
      },
    }
  };
</script>

<style></style>



