<template>
  <!--
    	luoyiming
    	2019-10-26
    	--/--
    -->
  <div class="spec-many-type">
    <!---->
    <div class="spec-wrap">
      <!---->
      <div v-if="!form.isSpecLocked">
        <el-button size="small" class="el-icon-circle-plus" @click="onToggleAddGroupForm"> </el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  export default {
    data() {
      return {
        /**/
        showAddGroupBtn: true,
        /**/
        showAddGroupForm: false,
        /**/
        addGroupFrom: {
          specName: '',
          specValue: ''
        },
        groupLoading: false
      };
    },
    inject: ['form'],
    created() {
      /**/

    },
    methods: {

      /*/ */
      onToggleAddGroupForm: function() {
        let self = this;
        if(!Array.isArray(self.form.model.sku)){
          self.form.model.sku=[];
        }
       self.form.model.sku.push(
       {
         spec_name:'',
         product_price:'',
         bag_price:'',
         stock_num:'',
         cost_price:0,
         is_full:0
       })
       console.log(self.form.model.sku)
      },

      /**/
      onDeleteGroup: function(index) {
        var self = this;
        self
          .$confirm('?', '', {
            type: 'warning'
          })
          .then(() => {
            // 
            self.form.model.spec_many.spec_attr.splice(index, 1);
            // 
            self.buildSkulist();
          });
      },

      /**/
      onDeleteValue: function(index, itemIndex) {
        let self = this;

        if (self.form.isSpecLocked) {
          self.$message({
            message: '',
            type: 'warning'
          });
          return;
        }

        self
          .$confirm('?', '', {
            type: 'warning'
          })
          .then(() => {
            // 
            self.form.model.spec_many.spec_attr[index].spec_items.splice(itemIndex, 1);
            // 
            self.buildSkulist();
          });
      }
    }
  };
</script>

<style scoped="scoped">
  .spec-many-type {
    margin-left: 180px;
    margin-top: 16px;
    padding: 20px;
    border: 1px solid #e5ecf4;
    background: #f6f9fc;
  }

  .spec-wrap .spec-hd {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    font-weight: bold;
  }

  .spec-wrap .spec-hd .el-icon-delete-solid {
    font-size: 16px;
    color: #999999;
  }

  .spec-wrap .min-spc {
    border: 1px solid #dfecf8;
  }

  .spec-wrap .spec-bd {
    padding: 5px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    border-top: 1px solid #dfecf8;
    background: #ffffff;
  }

  .spec-wrap .spec-bd .el-tag {
    color: #333333;
  }

  .spec-wrap .spec-bd .item {
    position: relative;
    padding: 5px;
  }

  .spec-wrap .spec-bd .item input {
    padding-right: 30px;
  }

  .spec-wrap .spec-hd a,
  .spec-wrap .spec-hd .svg-icon,
  .spec-wrap .spec-bd .item .svg-icon {
    display: block;
    width: 16px;
    height: 16px;
  }

  .spec-wrap .spec-bd .item a {
    position: absolute;
    top: 6px;
    right: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .add-spec .from-box {
    display: flex;
    justify-content: flex-start;
  }

  .add-spec .item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 200px;
    margin-right: 20px;
  }

  .add-spec .item .key {
    display: block;
    white-space: nowrap;
  }
</style>



