<template>
  <!--
      luoyiming
      2019-10-24
      
  -->
  <div class="product-list">
    <!---->
    <div class="common-seach-wrap">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="sell"></el-tab-pane>
        <el-tab-pane label="" name="lower">
          <span slot="label"> <el-tag size="mini">{{product_count.lower}}</el-tag></span>
        </el-tab-pane>
      </el-tabs>
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.category_id" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option v-for="(item, index) in categoryList" :key="index" :label="item.name" :value="item.category_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=""><el-input size="small" v-model="searchForm.product_name" placeholder=""></el-input></el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="common-level-rail"><el-button size="small" type="primary" icon="el-icon-plus" v-auth="'/product/store/product/add'" @click="addClick"></el-button></div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="product_name" label="" width="400px">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image[0].file_path" alt="" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                  <div class="price">{{ scope.row.product_price }}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="category.name" label=""></el-table-column>
          <el-table-column prop="sales_actual" label=""></el-table-column>
          <el-table-column prop="product_stock" label=""></el-table-column>
          <el-table-column prop="product_status.text" label="">
            <template slot-scope="scope">
              <span :class="{ green: scope.row.product_status.value == 10, gray: scope.row.product_status.value == 20 }">{{ scope.row.product_status.text }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column prop="product_sort" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="160">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/store/product/edit'"></el-button>
              <el-button @click="undercarriage(scope.row,20)" type="text" size="small" v-auth="'/product/store/product/state'" v-if="scope.row.product_status.value!=20"></el-button>
              <el-button @click="undercarriage(scope.row,10)" type="text" size="small" v-auth="'/product/store/product/state'" v-else></el-button>
              <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/product/store/product/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="curPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalDataNumber"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import PorductApi from '@/api/product.js';
export default {
  components: {},
  data() {
    return {
      /**/
      activeName: 'sell',
      /**/
      activeIndex: '0',
      /**/
      loading: true,
      /**/
      pageSize: 10,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      searchForm: {
        product_name: '',
        category_id: ''
      },
      /**/
      tableData: [],
      /**/
      categoryList: [],
      /**/
      product_count: {}
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.loading = true;
      self.curPage = val;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.pageSize = val;
      this.getData();
    },

    /**/
    handleClick(tab, event) {
      let self = this;
      self.curPage = 1;
      self.getData();
    },

    /**/
    getData() {
      let self = this;
      let Params = self.searchForm;
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      Params.type = self.activeName;
      self.loading = true;
      PorductApi.storeProductList(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.categoryList = data.data.category;
          self.totalDataNumber = data.data.list.total;
          self.product_count = data.data.product_count;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    onSubmit() {
      this.curPage = 1;
      this.getData();
    },

    /**/
    addClick() {
      this.$router.push('/product/store/product/add');
    },

    /**/
    editClick(row) {
      this.$router.push({
        path: '/product/store/product/edit',
        query: {
          product_id: row.product_id,
          scene: 'edit'
        }
      });
    },
    /* */
    undercarriage(row,state){
      let self = this;
      let war="";
      let war_='';
      if(state==20){
        war="",
        war_=''
      }else if(state==10){
        war="",
        war_=''
      }
      self
        .$confirm(""+war+"?", '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.storeStateProduct({
            product_id: row.product_id,
            state
          }).then(data => {
            self.$message({
              message: war_+'',
              type: 'success'
            });
            self.getData();
          });
        });
    },
    /**/
    copyClick(row) {
      this.$router.push({
        path: '/product/product/edit',
        query: {
          product_id: row.product_id,
          scene: 'copy'
        }
      });
    },

    /**/
    deleteClick: function(row) {
      let self = this;
      self
        .$confirm('?', '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.storeDelProduct({
            product_id: row.product_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
    }
  }
};
</script>

<style></style>



