<template>
  <!--
    	luoyiming
    	2020-06-20
    	diy--
    -->
  <div>
    <div class="common-form"><span>{{ curItem.name }}</span></div>
    <el-form size="small" :model="curItem" label-width="100px">
      <!---->
      <el-form-item label="">
        <div class="d-s-c">
            <el-color-picker v-model="curItem.style.background"></el-color-picker>
            <el-button type="button" style="margin-left: 10px;" @click.stop="$parent.onEditorResetColor(curItem.style, 'btnColor', '#ffffff')">
              
            </el-button>
        </div>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <el-radio-group v-model="curItem.style.rowsNum">
            <el-radio label="1">1</el-radio>
            <el-radio label="2">2</el-radio>
          </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <div class="param-img-item" :key="index" v-for="(navBar, index) in curItem.data">
          <div class="icon">
            <img v-img-url="navBar.imageUrl" alt="" @click="$parent.onEditorSelectImage(navBar, 'imageUrl')">
          </div>
          <p class="tc gray9">85*85</p>
          <div class="url-box">
            <span class="key-name"></span>
            <el-input v-model="navBar.title"></el-input>
          </div>
          <div class="url-box">
            <span class="key-name"></span>
            <el-input v-model="navBar.text"></el-input>
          </div>
          <div class="url-box">
            <span class="key-name"></span>
            <div class="d-s-c">
                <el-color-picker v-model="navBar.color"></el-color-picker>
                <el-button type="button" style="margin-left: 10px;" @click.stop="$parent.onEditorResetColor(curItem.style, 'btnColor', '#ffffff')">
                  
                </el-button>
            </div>
          </div>
          <div class="d-s-c">
            <div class="url-box flex-1">
            <span class="key-name"></span>
            <el-input v-model="navBar.name"></el-input>
          </div>
          <div class="url-box ml10">
            <el-button type="primary" @click="changeLink(index)"></el-button>
          </div>
          </div>
        </div>
        <div class="d-c-c" v-if="curItem.data.length==1&&curItem.style.rowsNum==2">
          <el-button @click="$parent.onEditorAddData"></el-button>
        </div>
      </el-form-item>
    </el-form>
    <Setlink v-if="is_linkset" :is_linkset="is_linkset" @closeDialog="closeLinkset"></Setlink>
  </div>
</template>

<script>
    import Setlink from '@/components/setlink/Setlink';
  export default {
      components: {
          Setlink,
      },
    data() {
      return {
        /**/
          is_linkset: false,
          index: null,
      };
    },
    props: ['curItem', 'selectedIndex', 'opts'],
    methods: {

      /**/
        changeLink(index) {
            this.is_linkset = true;
            this.index = index;
        },

      /**/
        closeLinkset(e) {
            this.is_linkset = false;
            this.curItem.data[this.index].linkUrl=e.url;
            this.curItem.data[this.index].name = '' + ' ' + e.type + ' ' + e.name;
        },
    }
  };
</script>

<style>

</style>



