<template>
  <div class="user clearfix">
    <div class="common-seach-wrap fr"  v-auth="'/auth/shop'">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.search" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="shop_supplier_id" label="ID" width="90"></el-table-column>
          <el-table-column prop="name" label="">
            <template slot-scope="scope">
              <span>{{scope.row.name}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="link_name" label="" width="90"></el-table-column>
          <el-table-column prop="link_phone" label="" width="120"></el-table-column>
          <el-table-column prop="address" label=""></el-table-column>
          <el-table-column prop="create_time" label="" width="150"></el-table-column>
          <el-table-column fixed="right" label="" width="150">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/supplier/supplier/edit'"></el-button>
              <el-button @click="qrcode(scope.row)" type="text" size="small" v-auth="'/supplier/supplier/edit'"></el-button>
              <el-button @click="settingClick(scope.row)" type="text" size="small" v-auth="'/supplier/supplier/setting'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>
    <Qrcode :open='isqrcode' :code_id='code_id' @close="closeQrcode"></Qrcode>
  </div>
</template>

<script>
  import SupplierApi from '@/api/supplier.js';
  import Qrcode from './dialog/Qrcode.vue';
  export default {
    components: {
      Qrcode
    },
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        storeList: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          shop_id: '',
          search: ''
        },
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        userModel: {},
        isqrcode:false,
        code_id:''
      };
    },
    created() {
      /**/
      this.getTableList();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },

      /**/
      getTableList() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        SupplierApi.supplierList(Params, true)
          .then(res => {
            self.loading = false;
            self.tableData = res.data.list.data;
            self.totalDataNumber = res.data.list.total;
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /**/
      onSubmit() {
        this.curPage = 1;
        this.getTableList();
      },

      qrcode(row){
        let self = this;
        self.code_id = row.shop_supplier_id;
        self.isqrcode=true;
      },
      closeQrcode(){
        let self = this;
        self.isqrcode=false;
      },
      /**/
      editClick(row) {
        let self = this;
        let params = row.shop_supplier_id;
        this.$router.push({
          path: '/supplier/supplier/edit',
          query: {
            shop_supplier_id: params
          }
        })
      },
      /**/
      settingClick(row) {
        let self = this;
        let params = row.shop_supplier_id;
        this.$router.push({
          path: '/supplier/supplier/setting',
          query: {
            shop_supplier_id: params
          }
        })
      },
    }
  };
</script>

<style></style>



