<template>
  <!--
    	wangxw
    	2019-10-26
    	
    -->
  <div class="product">
     <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <div class="common-level-rail">
            <el-button size="small" type="primary" @click="addClick" icon="el-icon-plus" v-auth="'/product/store/category/Add'"></el-button>
          </div>
          <!---->
          <div class="product-content">
            <div class="table-wrap">
              <el-table size="small" :data="tableData" row-key="category_id" default-expand-all :tree-props="{children: 'child'}"
                style="width: 100%" v-loading="loading">
                <el-table-column prop="name" label="" width="180"></el-table-column>
                <el-table-column prop="" label="" width="180">
                  <template slot-scope="scope">
                    <img v-img-url="hasImages(scope.row.images)" alt="" width="50px" />
                  </template>
                </el-table-column>
                <el-table-column prop="sort" label=""></el-table-column>
                <el-table-column prop="create_time" label=""></el-table-column>
                <el-table-column fixed="right" label="" width="100">
                  <template slot-scope="scope">
                    <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/store/category/Edit'"></el-button>
                    <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/product/store/category/Delete'"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!---->
          <div class="product-content">
            <div class="table-wrap">
              <el-table size="small" :data="tableData" row-key="category_id" default-expand-all :tree-props="{children: 'child'}"
                style="width: 100%" v-loading="loading">
                <el-table-column prop="sort" label=""></el-table-column>
                <el-table-column prop="name" label="" width="180"></el-table-column>
                <el-table-column prop="" label="" width="180">
                  <template slot-scope="scope">
                    <img v-img-url="hasImages(scope.row.images)" alt="" width="50px" />
                  </template>
                </el-table-column>
                <el-table-column prop="create_time" label=""></el-table-column>
                <el-table-column fixed="right" label="" width="100">
                  <template slot-scope="scope">
                    <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/store/category/Edit'"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    <!---->




    <!---->
    <Add v-if="open_add" :open_add="open_add" :addform="categoryModel" @closeDialog="closeDialogFunc($event, 'add')"></Add>
    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :editform="categoryModel" @closeDialog="closeDialogFunc($event, 'edit')"></Edit>

  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Add from './Add.vue';
  import Edit from './Edit.vue';
  export default {
    components: {
      Add,
      Edit
    },
    data() {
      return {
        /**/
        loading: true,
        activeName:'first',
        /**/
        tableData: [],
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        categoryModel: {
          catList: [],
          model: {}
        },
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {

      hasImages(e) {
        if (e) {
          return e.file_path;
        } else {
          return '';
        }
      },
      handleClick(){
       this.getData();
      },
      /**/
      getData() {
        let self = this;
        self.loading=true;
        if(self.activeName=='first'){
          PorductApi.storeCatList({}, true)
            .then(data => {
              self.loading = false;
              self.tableData = data.data.list;
              self.categoryModel.catList = self.tableData;
            })
            .catch(error => {
              self.loading = false;
            });
        }else{
          PorductApi.storeCatSP({}, true)
            .then(data => {
              self.loading = false;
              self.tableData = data.data.list;
              self.categoryModel.catList = self.tableData;
            })
            .catch(error => {
              self.loading = false;
            });
        }

      },
      /**/
      addClick() {
        this.open_add = true;
      },

      /**/
      editClick(item) {
        this.categoryModel.model = item;
        this.open_edit = true;
      },

      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          PorductApi.storeCatDel({
            category_id: row.category_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
      },
    }
  };
</script>

<style></style>



