<template>
  <!--
    	luoyiming
    	2019-10-26
    	-
    -->
  <div class="product-add" v-loading="loading">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="180px" v-if="!loading">
      <!---->
      <Basic></Basic>
      <!---->
      <Spec></Spec>

      <!-- -->
      <Attr></Attr>

      <!-- -->
      <Ingredients></Ingredients>
      <!---->
      <Content></Content>
      <!---->
      <Buyset></Buyset>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Basic from './part/Basic.vue';
  import Attr from './part/Attr.vue';
  import Ingredients from './part/Ingredients.vue';
  import Spec from './part/Spec.vue';
  import Content from './part/Content.vue';
  import Buyset from './part/Buyset.vue';
  import {
    formatModel
  } from '@/utils/base.js';
  export default {

    components: {
      /**/
      Basic,
      /**/
      Spec,
      /* */
      Attr,
      /**/
      Ingredients,
      /**/
      Content,
      /**/
      Buyset,
    },
    data() {

      return {
        /* */
        old_audit: 20,
        /*ID*/
        product_id: 0,
        /**/
        scene: 'edit',
        /**/
        loading: true,
        /**/
        save_loading: false,
        /*form*/
        form: {
          model: {},
          /**/
          category: [],
          feed: [],
          special: [],
          /**/
          gradeList: [],
          /**/
          specData: null,
          /**/
          isSpecLocked: false,
        },
        /**/
        model: {
          /**/
          product_name: '',
          /**/
          category_id: null,
          /**/
          image: [],
          /**/
          selling_point: '',
          /*,1020*/
          spec_type: 10,
          /*,2010*/
          deduct_stock_type: 20,
          /**/
          is_alone_grade: 0,
          /**/
          sku: [{
            product_price: '',
            stock_num: '',
            product_weight: '',
            cost_price: ''
          }],
          product_attr: [],
          product_feed: [],
          /*  */
          min_buy: 1,
          /*  */
          product_unit: '',
          /**/
          content: '',
          /**/
          product_status: 10,
          /**/
          sales_initial: 0,
          /*100*/
          product_sort: 100,
          /**/
          limit_num: 0,
          special_id: 0
        }
      };
    },
    provide: function() {
      return {
        form: this.form
      };
    },
    created() {
      /**/
      this.product_id = this.$route.query.product_id;
      this.scene = this.$route.query.scene;
      this.getData();
    },
    methods: {
      /**
       * 
       */
      getData: function() {
        let self = this;
        PorductApi.storeGetEditData({
              product_id: self.product_id,
              scene: self.scene
            },
            true
          )
          .then(res => {
            self.loading = false;
            Object.assign(self.form, res.data);
            self.form.model.product_status = res.data.model.product_status.value;
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /*JSON*/
      convertJson(list) {
        let obj = {};
        list.forEach(item => {
          obj[item.grade_id] = item.product_equity;
        });
        return JSON.stringify(obj);
      },

      /**/
      onSubmit: function() {
        let self = this;
        self.$refs.form.validate(valid => {
          let params = formatModel(self.model, self.form.model);
          if (valid) {
            let params = formatModel(self.model, self.form.model);
            params.scene = self.scene;
            params.image = self.ImgKeepId(params.image);
            params.product_id = self.product_id;
            params.sku = self.form.model.sku;
            self.save_loading = true;
            PorductApi.storeEditProduct({
                product_id: self.product_id,
                params: JSON.stringify(params)
              }, true)
              .then(data => {
                self.save_loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.cancelFunc();
              })
              .catch(error => {
                self.save_loading = false;
              });
          }
        });
      },

      /*id*/
      ImgKeepId(list) {
        let arr = [];
        for (let i = 0, length = list.length; i < length; i++) {
          let obj = {
            image_id: list[i].image_id,
            file_id: list[i].file_id
          };
          arr.push(obj);
        }
        return arr;
      },

      /**/
      Draft() {
        let self = this;
        self.form.model.product_status = 30;
        self.onSubmit();
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 50px;
  }

  .mb50 {
    margin-bottom: 50px;
  }
</style>



