<template>
  <!--
    	wangxw
    	2019-10-26
    	-
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false"
    :close-on-press-escape="false">
    <el-form size="small" :model="form" :rules="formRules" ref="form">
     <el-form-item label="" prop="type_name" :label-width="formLabelWidth">
       <el-input v-model="form.type_name" autocomplete="off"></el-input>
     </el-form-item>
     <el-form-item label="" prop="min_num" :label-width="formLabelWidth">
       <el-input v-model.number="form.min_num" autocomplete="off"></el-input>
     </el-form-item>
     <el-form-item label="" prop="max_num" :label-width="formLabelWidth">
       <el-input v-model.number="form.max_num" autocomplete="off"></el-input>
     </el-form-item>
     <el-form-item label="" prop="sort" :label-width="formLabelWidth">
       <el-input v-model.number="form.sort" autocomplete="off"></el-input>
     </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="addUser" :loading="loading"> </el-button>
    </div>
    </el-dialog>
</template>

<script>
  import StoreApi from '@/api/store.js';
  export default {
    components: {
    },
    data() {
      return {
        form: {
         type_id: 0,
         type_name: '',
         min_num:1,
         max_num:1,
         sort: 100,
        },
        file_path: '',
        formRules: {
          type_name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          sort: [{
            required: true,
            message: ''
          }, {
            type: 'number',
            message: ''
          }]
        },
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false,
        loading: false,
        /**/
        isupload: false,
      };
    },
    props: ['open_edit', 'editform'],
    created() {
      this.dialogVisible = this.open_edit;
      this.form.type_id = this.editform.model.type_id;
      this.form.type_name = this.editform.model.type_name;
      this.form.min_num = this.editform.model.min_num;
      this.form.max_num = this.editform.model.max_num;
      this.form.sort = this.editform.model.sort;
    },
    methods: {
      /**/
      addUser() {
        let self = this;
        let params = self.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            StoreApi.editType(params, true).then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.dialogFormVisible(true);
            }).catch(error => {
              self.loading = false;
            });
          }
        });
      },
      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          })
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          })
        }
      },
    }
  };
</script>

<style>
  .img {
    margin-top: 10px;
  }
</style>



