<template>
  <!--
    	wangxw
    	2019-10-26
    	
    -->
  <div class="product">
    <div>
      <el-form size="small" :inline="true" :model="form" class="demo-form-inline d-s-c">
        <el-form-item label="">
          <el-input v-model="form.search" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="form.area_id" placeholder="">
            <el-option v-for="(item, index) in area_list" :key="index" :label="item.area_name" :value="item.area_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="form.type_id" placeholder="">
            <el-option v-for="(item, index) in type_list" :key="index" :label="item.type_name" :value="item.type_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" @click="addClick" icon="el-icon-plus" v-auth="'/store/table/table/add'"></el-button>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" row-key="type_id"  style="width: 100%" v-loading="loading">
          <el-table-column prop="area_name" label=""></el-table-column>
          <el-table-column prop="type_name" label=""></el-table-column>
          <el-table-column prop="table_no" label=""></el-table-column>
          <el-table-column prop="table_no" label="">
            <template slot-scope="scope">
              {{scope.row.min_num}}-{{scope.row.max_num}}
            </template>
          </el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="190">
            <template slot-scope="scope">
              <el-button @click="qrcodeClick(scope.row)" type="text" size="small"
                v-auth="'/store/table/table/edit'"></el-button>
              <el-button @click="editClick(scope.row)" type="text" size="small"
                v-auth="'/store/table/table/edit'"></el-button>
                <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/store/table/table/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <Add v-if="open_add" :open_add="open_add" :type='type_list' :area_list="area_list" :addform="categoryModel" @closeDialog="closeDialogFunc($event, 'add')">
    </Add>
    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :editform="categoryModel"  :type='type_list' :area_list="area_list"
      @closeDialog="closeDialogFunc($event, 'edit')"></Edit>

  </div>
</template>

<script>
  import StoreApi from '@/api/store.js';
  import Add from './add.vue';
  import Edit from './edit.vue';
  import qs from 'qs';
  export default {
    components: {
      Add,
      Edit
    },
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        categoryModel: {
          model:''
        },
        form:{search:'',area_id:'',type_id:''},
        type_list:[],
        area_list:[],
        source: 'wx',
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      handleClick() {
        this.getData();
      },
      /**/
      getData() {
        let self = this;
        self.loading = true;
        let params= self.form
          StoreApi.tablelist(params, true)
            .then(data => {
              self.loading = false;
              self.tableData = data.data.list.data;
              self.type_list = data.data.type_list;
              self.area_list = data.data.area_list;
              self.categoryModel = data.data.list.data;
            })
            .catch(error => {
              self.loading = false;
            });
      },
      /**/
      addClick() {
        this.open_add = true;
      },

      /**/
      editClick(item) {
        this.categoryModel.model = item;
        this.open_edit = true;
      },

      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          StoreApi.deleteTable({
            table_id: row.table_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
      },
      //
      qrcodeClick(row){
        let baseUrl = window.location.protocol + '//' + window.location.host;
        let params = {
          id: row.table_id,
          source: this.source
        };
        window.location.href = baseUrl + '/index.php/shop/store.table.table/qrcode?' + qs.stringify(params);
      },
      onSubmit(){
          this.curPage = 1;
          this.getData();
      }
    }
  };
</script>

<style></style>



