<template>
  <!--
      	luoyiming
      	2020-06-01
      	-
      -->
  <div>
    <div class="common-level-rail"><el-button size="small" type="primary" icon="el-icon-plus" @click="addAd"></el-button></div>
    <div class="table-wrap">
      <el-table :data="tableData" style="width: 100%" v-loading="loading">
        <el-table-column prop="menu_id" label="ID" width="100"></el-table-column>
         <el-table-column prop="title" label="">
          <template slot-scope="scope">
            <div class="text-ellipsis" :title="scope.row.title">{{scope.row.title}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="" width="250">
          <template slot-scope="scope">
            <img v-img-url="'image_url',scope.row" width="50" height="50" />
          </template>
        </el-table-column>
        <el-table-column prop="sort" label="" width="100"></el-table-column>
        <el-table-column prop="status" label="" width="80">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              :active-value="1"
              :inactive-value="0"
              @change="changeStatus(scope.row)"
              active-color="#13ce66"
              inactive-color="#cccccc"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="" width="140"></el-table-column>
        <el-table-column prop="update_time" label="" width="140"></el-table-column>
        <el-table-column prop="name" label="" width="90">
          <template slot-scope="scope">
            <el-button @click="editAd(scope.row)" type="text" size="small"></el-button>
            <el-button @click="deleteAd(scope.row)" type="text" size="small" v-if="scope.row.app_id>0"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="pagination">
        <el-pagination background :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import PageApi from '@/api/page.js';
export default {
  components: {},
  data() {
    return {
      /**/
      tableData: [],
      /**/
      open_add: false,
      /**/
      open_edit: false,
      /**/
      userModel: {},
      commentData: [],
      loading: true,
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1
    };
  },
  created() {
    /**/
    this.getTableList();
  },
  methods: {

    /**/
    getTableList() {
      let self = this;
      let Params = {};
      Params.page = self.curPage;
      PageApi.menuList(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    addAd() {
      this.$router.push({
        path: '/page/page/mymenu/add'
      });
    },

    /**/
    editAd(row) {
      let params = row.menu_id;
      this.$router.push({
        path: '/page/page/mymenu/edit',
        query: {
          menu_id: params
        }
      });
    },
    /**/
    changeStatus(item){
      let self = this;
      const loading = self.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      PageApi.editMenu({
        menu_id: item.menu_id,
        status: item.status
      })
        .then(data => {
          loading.close();
        })
        .catch(data => {
          loading.close();
          self.$message({
            message: '',
            type: 'warning'
          });
        });
    },
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.getData();
    },

    /**/
    deleteAd(row) {
      let self = this;
      self
        .$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          self.loading = true;
          PageApi.deleteMenu(
            {
              menu_id: row.menu_id
            },
            true
          )
            .then(data => {
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.loading = false;
              self.getTableList();
            })
            .catch(error => {});
        })
        .catch(() => {});
    },


    handleClick(tab, event) {},



    /**/
    closeDialogFunc(e, f) {
      if (f == 'add') {
        this.open_add = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        if (e.type == 'success') {
          this.getTableList();
        }
      }
    }
  }
};
</script>

<style></style>



