<template>
  <!--
      luoyiming
      2019-10-25
      --
  -->
  <el-dialog title="" :visible.sync="dialogVisible" @close='dialogFormVisible' :close-on-click-modal="false"
    :close-on-press-escape="false">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <el-form size="small" :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.balance" autocomplete="off" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="recharge.balance.mode">
              <el-radio label="inc"></el-radio>
              <el-radio label="dec"></el-radio>
              <el-radio label="final"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="recharge.balance.money" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="recharge.balance.remark" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!-- <el-tab-pane label="" name="second">
        <el-form size="small" :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.points" autocomplete="off" disabled="disabled"></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="recharge.points.mode">
              <el-radio label="inc"></el-radio>
              <el-radio label="dec"></el-radio>
              <el-radio label="final"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="recharge.points.value" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="recharge.points.remark" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </el-tab-pane> -->
    </el-tabs>


    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="addUser(form.user_id)" :loading="loading"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import UserApi from '@/api/user.js';
  export default {
    data() {
      return {
        loading: false,
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false,
        /**/
        activeName: 'first',
        recharge: {
          balance: {
            mode: 'inc',
            remark: '',
          },
          points: {
            mode: 'inc',
            remark: '',
          }
        },
        source: 0,
      };
    },
    props: ['open_add', 'form'],
    created() {
      this.dialogVisible = this.open_add;
    },
    methods: {

      /**/
      handleClick(tab, event) {
        this.source = tab.index;
      },
      /**/
      addUser(e) {
        let self = this;
        let params = self.recharge;
        let user_id = e;
        let source = self.source;
        self.loading = true;
        UserApi.userRecharge({
            params: params,
            user_id: user_id,
            source: source
          }, true).then(data => {
            self.loading = false;
            if (data.code == 1) {
              self.$message({
                message: '',
                type: 'success'
              });
              self.dialogFormVisible(true);
            } else {
              self.loading = false;
              self.$message.error('');
            }
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          })
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          })
        }
      }

    }
  };
</script>

<style></style>



