<template>
  <!--
      	-
      -->
  <div class="common-seach-wrap">
    <!---->
    <spec v-if="activeName==='spec'"></spec>
    <!---->
    <attr v-if="activeName==='attr'"></attr>
    <!---->
    <unit v-if="activeName==='unit'"></unit>
    <!---->
    <feed v-if="activeName==='feed'"></feed>
  </div>
</template>
<script>
import bus from '@/utils/eventBus.js';
import spec from './spec/index.vue';
import attr from './attr/index.vue';
import unit from './unit/index.vue';
import feed from './feed/index.vue';

export default {
  components: {
    spec,
    attr,
    unit,
    feed,
  },
  data() {
    return {
      /**/
      loading: true,
      form: {},
      /**/
      param: {},
      /**/
      activeName: 'spec',
      /**/
      sourceList: [
        {
          key: 'spec',
          value: '',
          path: '/product/expand/spec/index'
        },
        {
          key: 'attr',
          value: '',
          path: '/product/expand/attr/index'
        },
        {
          key: 'feed',
          value: '',
          path: '/product/expand/feed/index'
        },
        {
          key: 'unit',
          value: '',
          path: '/product/expand/unit/index'
        },
      ],
      /**/
      tabList: [],
    };
  },
  watch: {

    //
    $route(to, from) {
      this.init();
    }
  },
  created() {
    this.init()

  },
  beforeDestroy() {
    //
    bus.$emit('tabData', {active: null, tab_type: 'expand', list: []});
    bus.$off('activeValue');
  },
  methods: {
    /**/
    init() {
      this.tabList = this.authFilter();

      if (this.tabList.length > 0) {
        this.activeName = this.tabList[0].key;
      }

      if (this.$route.query.type != null) {
        this.activeName = this.$route.query.type;
      }

      /**/
      bus.$on('activeValue', res => {
        if (this.is_third_param) {
          this.param.user_id = '';
          this.is_third_param = false;
        }
        this.activeName = res;
      });

      //
      let params = {
        active: this.activeName,
        list: this.tabList,
        tab_type: 'expand'
      };
      bus.$emit('tabData', params);
    },
    /**/
    authFilter() {
      let list = [];
      for (let i = 0; i < this.sourceList.length; i++) {
        let item = this.sourceList[i];
        if (this.$filter.isAuth(item.path)) {
          list.push(item);
        }
      }
      return list;
    },

  }
};
</script>

<style>
.operation-wrap {
  height: 124px;
  border-radius: 8px;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 30px 30px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  overflow: hidden;
  background: #909399;
  background-size: 100% 100%;
  color: #fff;
}
</style>



