<template>
  <!--
    	luoyiming
    	2019-10-26
    	-
    -->
  <div class="product-add" v-loading="loading">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="180px" v-if="!loading">
      <!---->
      <Basic></Basic>
      <!---->
      <Spec></Spec>
      <!---->
      <Content></Content>
      <!---->
      <Buyset></Buyset>
      <!---->
      <div class="common-button-wrapper" :style="old_audit == 0 || old_audit == 20? 'height: 100px;':'height: 50px'">
        <Audit v-if="old_audit == 0 || old_audit == 20"></Audit>
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :disabled="save_loading" v-if="old_audit==10">
        </el-button>
        <el-button size="small" type="primary" @click="onSubmit" :disabled="save_loading" v-else></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Basic from './part/Basic.vue';
  import Spec from './part/Spec.vue';
  import Content from './part/Content.vue';
  import Buyset from './part/Buyset.vue';
  import Audit from './part/Audit.vue';
  import {
    formatModel
  } from '@/utils/base.js';
  export default {

    components: {
      /**/
      Basic,
      /**/
      Spec,
      /**/
      Content,
      /**/
      Buyset,
      /**/
      Audit
    },
    data() {

      return {
        /* */
        old_audit: 20,
        /*ID*/
        product_id: 0,
        /**/
        scene: 'edit',
        /**/
        loading: true,
        /**/
        save_loading: false,
        /*form*/
        form: {
          model: {},
          /**/
          category: [],
          /**/
          delivery: [],
          /**/
          gradeList: [],
          /**/
          specData: null,
          /**/
          isSpecLocked: false,
          /**/
          basicSetting: {},
          /**/
          agentSetting: {}
        },
        /**/
        model: {
          /**/
          product_name: '',
          /**/
          category_id: null,
          /**/
          image: [],
          /**/
          selling_point: '',
          /*,1020*/
          spec_type: 10,
          /*,2010*/
          deduct_stock_type: 20,
          /**/
          is_alone_grade: 0,
          /**/
          sku: {
            /**/
            supplier_price: 0,
            /**/
            product_price: 0,
            /**/
            line_price: 0,
            /**/
            stock_num: 0,
            /**/
            product_weight: 0,
            /**/
            product_no: '',
          },
          /**/
          spec_many: {
            /**/
            spec_attr: [],
            /**/
            spec_list: []
          },
          /**/
          content: '',
          /*ID*/
          delivery_id: '',
          /**/
          product_status: 10,
          /**/
          sales_initial: 0,
          /*100*/
          product_sort: 100,
          /*,10*/
          is_points_gift: 1,
          /*,10*/
          is_points_discount: 1,
          /**/
          max_points_discount: 0,
          /*,1,0*/
          is_enable_grade: 1,
          /*,1020*/
          alone_grade_type: 10,
          /**/
          grade_ids: [],
          /**/
          is_agent: 0,
          /*,01*/
          is_ind_agent: 0,
          /*,1020*/
          agent_money_type: 10,
          /**/
          first_money: null,
          /**/
          second_money: null,
          /**/
          third_money: null,
          /**/
          is_virtual: 0,
          /**/
          limit_num: 0,
          audit_status: '',
          audit_remark: '',
          /**/
          virtual_auto: 0,
          /**/
          virtual_content: ''
        }
      };
    },
    provide: function() {
      return {
        form: this.form
      };
    },
    created() {
      /**/
      this.product_id = this.$route.query.product_id;
      this.scene = this.$route.query.scene;
      this.getData();
    },
    methods: {
      /**
       * 
       */
      getData: function() {
        let self = this;
        PorductApi.getEditData({
              product_id: self.product_id,
              scene: self.scene
            },
            true
          )
          .then(res => {
            self.loading = false;
            self.old_audit = res.data.model.audit_status;
            /**/
            if (res.data.model.alone_grade_equity != null && typeof res.data.model.alone_grade_equity !=
              'undefined') {
              let equitys = res.data.model.alone_grade_equity;
              res.data.gradeList.forEach(item => {
                item.product_equity = equitys[item.grade_id];
              });
            }
            Object.assign(self.form, res.data);

            if (res.data.specData) {
              self.$set(self.form.model, 'spec_many', res.data.specData);
            } else {
              self.$set(self.form.model, 'spec_many', {
                spec_attr: [],
                spec_list: []
              });
              self.form.model.sku = res.data.model.sku[0];
            }
            if (self.form.model.audit_status != 10 && self.form.model.audit_status != 20) {
              self.form.model.audit_status = 10;
            }
            self.form.model.product_status = res.data.model.product_status.value;
            // id
            for (let i = 0; i < self.form.model.grade_ids.length; i++) {
              self.form.model.grade_ids[i] = parseInt(self.form.model.grade_ids[i]);
            }
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /*JSON*/
      convertJson(list) {
        let obj = {};
        list.forEach(item => {
          obj[item.grade_id] = item.product_equity;
        });
        return JSON.stringify(obj);
      },

      /**/
      onSubmit: function() {
        let self = this;
        self.$refs.form.validate(valid => {
          let params = formatModel(self.model, self.form.model);
          if (valid) {
            let params = formatModel(self.model, self.form.model);
            params.scene = self.scene;
            params.image = self.ImgKeepId(params.image);
            params.product_id = self.product_id;
            params.alone_grade_equity = self.convertJson(self.form.gradeList);
            self.save_loading = true;
            PorductApi.editProduct({
                product_id: self.product_id,
                params: JSON.stringify(params)
              }, true)
              .then(data => {
                self.save_loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.cancelFunc();
              })
              .catch(error => {
                self.save_loading = false;
              });
          }
        });
      },

      /*id*/
      ImgKeepId(list) {
        let arr = [];
        for (let i = 0, length = list.length; i < length; i++) {
          let obj = {
            image_id: list[i].image_id,
            file_id: list[i].file_id
          };
          arr.push(obj);
        }
        return arr;
      },

      /**/
      Draft() {
        let self = this;
        self.form.model.product_status = 30;
        self.onSubmit();
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 50px;
  }

  .mb50 {
    margin-bottom: 50px;
  }
</style>



