<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="">
        <div>
          <el-radio v-model="form.seller_open" :label="'1'"></el-radio>
          <el-radio v-model="form.seller_open" :label="'0'"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="" v-if="form.seller_open==1">
        <el-select v-model="form.seller_printer_id" placeholder="">
          <el-option v-for="(item,index) in printerList" :key="index" :label="item.printer_name"
            :value="item.printer_id+''"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div>
          <el-radio v-model="form.buyer_open" :label="'1'"></el-radio>
          <el-radio v-model="form.buyer_open" :label="'0'"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="" v-if="form.buyer_open==1">
        <el-select v-model="form.buyer_printer_id" placeholder="">
          <el-option v-for="(item,index) in printerList" :key="index" :label="item.printer_name"
            :value="item.printer_id+''"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div>
          <el-radio v-model="form.room_open" :label="'1'"></el-radio>
          <el-radio v-model="form.room_open" :label="'0'"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="" v-if="form.room_open==1">
        <el-select v-model="form.room_printer_id" placeholder="">
          <el-option v-for="(item,index) in printerList" :key="index" :label="item.printer_name"
            :value="item.printer_id+''"></el-option>
        </el-select>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>



    </el-form>


  </div>

</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          buyer_open: '0',
          room_open:'0',
          seller_open:'0',
          buyer_printer_id: '',
          room_printer_id:'',
          seller_printer_id:''
        },
        checked: false,
        printerList: [],
        loading: false,


      };
    },
    created() {
      this.getData()
    },

    methods: {
      getData() {
        let self = this;
        SettingApi.printingDetail({}, true)
          .then(data => {
            let vars = data.data.vars.values;
            self.form.buyer_open = vars.buyer_open;
            self.form.seller_open = vars.seller_open;
            self.form.room_open = vars.room_open;
            self.form.buyer_printer_id = '' + vars.buyer_printer_id;
            self.form.room_printer_id = '' + vars.room_printer_id;
            self.form.seller_printer_id = '' + vars.seller_printer_id;
            self.printerList = data.data.vars.printerList;
            if (vars.order_status != null && vars.order_status[0] == 20) {
              self.checked = true;
            }

          })
          .catch(error => {});
      },
      //
      onSubmit() {
        let self = this;
        let params = this.form;
        self.loading = true;
        SettingApi.editPrinting(params, true)
          .then(data => {
            self.loading = false;
            self.$message({
              message: '',
              type: 'success'
            });
            // self.$router.push('/setting/Printing');

          })
          .catch(error => {
            self.loading = false;
          });
      },
      //
      handleCheckedCitiesChange(e) {
        let self = this;
        if (e) {
          self.form.order_status[0] = 20;
        } else {
          self.form.order_status = [];
        }
      },

    }

  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



