<template>
  <!--
    	luoyiming
    	2019-10-26
    	--
    -->
  <div class="buy-set-content pl16 pr16">
    <!---->
    <div class="common-form mt50"></div>
    <el-form-item v-if="form.model.product_status!=40" label="" :rules="[{ required: true, message: '' }]" prop="model.product_status">
      <el-radio-group  v-model="form.model.product_status">
        <el-radio :label="10"></el-radio>
        <el-radio :label="20"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-input type="number" min="0" v-model="form.model.sales_initial" class="max-w460"></el-input>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: ' ' }]" prop="model.product_sort">
      <el-input type="number" min="0" v-model="form.model.product_sort" class="max-w460"></el-input>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: ' ' }]" prop="model.limit_num">
      <el-input type="number" min="0" v-model="form.model.limit_num" class="max-w460"></el-input>
      <div class="gray9">0</div>
    </el-form-item>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        unit: '%',
        grade_unit: '%'
      };
    },
    created() {
      if(this.form.model.alone_grade_type == '20'){
        this.grade_unit = '';
      }
      if(this.form.model.agent_money_type == '20'){
        this.unit = '';
      }
    },
    inject: ['form'],
    methods: {
      /**/
      changeMoneyType: function(val) {
        if (val == '10') {
          this.unit = '%';
        } else {
          this.unit = '';
        }
      },
	  /**/
	  changeGradeType: function(val) {
	    if (val == '10') {
	      this.grade_unit = '%';
	    } else {
	      this.grade_unit = '';
	    }
	  }
    }
  };
</script>

<style></style>



