<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="150px">
      <div class="common-form"></div>
      <el-form-item label="">
        <el-radio-group v-model="form.menu_type">
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>

</template>

<script>
  import PageApi from '@/api/page.js';
  import Upload from '@/components/file/Upload';
  import { formatModel } from '@/utils/base.js';
  export default {
    data() {
      return {
        /**/
        loading: false,
        /*form*/
        form: {
         menu_type:1,
        },
        all_type: [],
        type: [],
        /**/
        isupload:false
      };
    },
    created() {
      this.getParams()
    },

    methods: {

      /**/
      getParams() {
        let self = this;
        PageApi.getbottomNav({}, true).then(res => {
            self.form=res.data.data;
            self.form.menu_type = parseInt(self.form.menu_type);
            self.loading=false;
          })
          .catch(error => {

          });
      },


      /**/
      onSubmit() {
        let self = this;
        let params = this.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            PageApi.postbottomNav(params, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/page/page/bottomnav');
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });

      },
      /**/
      chooseImg(e){
        this.type = e;
        this.isupload=true;
      },

      /**/
      returnImgsFunc(e){
        this.isupload=false;
         if (e != null && e.length > 0) {
        if(this.type == 'logo'){
           this.form.supplier_logo=e[0].file_path;
        }else if(this.type == 'image'){
           this.form.supplier_image=e[0].file_path;
        }
        }
      }
    }

  };
</script>
<style>
  .tips {
    color: #ccc;
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }
  input[type="number"]{
    -moz-appearance: textfield;
  }
</style>



