<template>
  <div class="pb50">
    <el-form size="small" ref="form" :model="form" label-width="150px">
      <el-form-item label="" prop="store_time"
        :rules="[{ required: true, message: '', trigger: 'change' }]">
        <el-time-picker is-range v-model="form.store_time" range-separator="" start-placeholder=""
          end-placeholder="" placeholder="" format="HH:mm" value-format="HH:mm">
        </el-time-picker>
      </el-form-item>
      <el-form-item label="" prop="pick_time"
        :rules="[{ required: true, message: '', trigger: 'change' }]">
        <el-time-picker is-range v-model="form.pick_time" range-separator="" start-placeholder=""
          end-placeholder="" placeholder="" format="HH:mm" value-format="HH:mm">
        </el-time-picker>
      </el-form-item>
      <el-form-item label="" prop="delivery_time"
        :rules="[{ required: true, message: '', trigger: 'change' }]">
        <el-time-picker is-range v-model="form.delivery_time" range-separator="" start-placeholder=""
          end-placeholder="" placeholder="" format="HH:mm" value-format="HH:mm">
        </el-time-picker>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.delivery_set">
          <el-checkbox v-for="(item,index) in deliver_type" :label="item.value" :key="index">{{item.name}}</el-checkbox>
         </el-checkbox-group>
        <div class="tips"></div>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.store_set">
          <el-checkbox v-for="(item,index) in store_type" :label="item.value" :key="index">{{item.name}}</el-checkbox>
         </el-checkbox-group>
        <div class="tips"></div>
      </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.bag_type">
        <el-radio :label="0"></el-radio>
        <el-radio :label="1"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item v-if="form.bag_type==1" label="" :rules="[{ required: true, message: '' }]" prop="bag_price">
      <div class=" d-s-c">
        <div class="max-w60">
          <el-input v-model="form.bag_price" type="number"></el-input>
        </div>
        <div class="ml10"></div>
      </div>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.storebag_type">
        <el-radio :label="0"></el-radio>
        <el-radio :label="1"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item v-if="form.storebag_type==1" label="" :rules="[{ required: true, message: '' }]" prop="bag_price">
      <div class=" d-s-c">
        <div class="max-w60">
          <el-input v-model="form.storebag_price" type="number"></el-input>
        </div>
        <div class="ml10"></div>
      </div>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="delivery_distance">
      <div class=" d-s-c">
        <div class="max-w60">
          <el-input class="" v-model="form.delivery_distance" type="number"></el-input>
        </div>
          <div class="ml10">km</div>
      </div>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="shipping_fee">
      <div class=" d-s-c">
        <div class="max-w60">
          <el-input v-model="form.shipping_fee" type="number"></el-input>
        </div>
        <div class="ml10"></div>
      </div>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="min_money">
      <div class=" d-s-c">
        <div class="max-w60">
          <el-input v-model="form.min_money" type="number"></el-input>
        </div>
        <div class="ml10"></div>
      </div>
    </el-form-item>
    <!---->
    <div class="common-button-wrapper">
      <el-button size="small" @click="cancelFunc"></el-button>
      <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
    </div>
        </el-form>
  </div>
</template>

<script>
  import SupplierApi from '@/api/supplier.js';
  import {
    formatModel
  } from '@/utils/base.js'
  export default {
    data() {
      return {
        /**/
        form: {
          shop_supplier_id:0,
          /**/
          store_time: '',
          pick_time:'',
          delivery_time:'',
          bag_type:0,
          bag_price:0,
          storebag_type:0,
          storebag_price:0,
          delivery_distance:'',
          shipping_fee:0,
          delivery_set: [],
          store_set: [],
          min_money:0
        },
        deliver_type:[{'name':'','value':10},{'name':'','value':20}],
        store_type:[{'name':'','value':30},{'name':'','value':40}],
        /**/
        loading: false
      };
    },
    created() {
      this.form.shop_supplier_id = this.$route.query.shop_supplier_id;
      this.getData();
    },
    methods: {
      /**/
      getData() {
        let self = this;
        SupplierApi.getsetSupplier({
            shop_supplier_id: self.form.shop_supplier_id
          }, true)
          .then(res => {
            self.form = formatModel(self.form, res.data.model);
            self.form.delivery_set = res.data.model.delivery_set;
            // 
            for(let i=0;i<self.form.delivery_set.length;i++){
              self.$set(self.form.delivery_set, i, parseInt(self.form.delivery_set[i]));
            }
            self.form.store_set = res.data.model.store_set;
            // 
            for(let i=0;i<self.form.store_set.length;i++){
              self.$set(self.form.store_set, i, parseInt(self.form.store_set[i]));
            }
            console.log(self.form)
          })
          .catch(error => {

          });
      },
      /**/
      onSubmit() {
        let self = this;
        let params = self.form;
        if (params.delivery_set.length < 1) {
          self.$message({
            message: '',
            type: 'warning'
          });
          return;
        }
        if (params.store_set.length < 1) {
          self.$message({
            message: '',
            type: 'warning'
          });
          return;
        }
        self.$refs.form.validate(valid => {
          if (valid) {

            console.log(params)
            self.loading = true;
            SupplierApi.setSupplier(params, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.cancelFunc();
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });
      },
      /**/
      cancelFunc() {
        this.$router.back(-1);
      }
    }
  };
</script>

<style>
  .max-w60{
    max-width: 60px;
  }
</style>



