<template>
  <el-dialog title="" :visible.sync="dialogVisible"  :close-on-click-modal="false"
    :close-on-press-escape="false" width="900px">
      <el-input class="mb16" size="small" v-model="addressData.name" placeholder=""></el-input>
      <el-input class="mb16" size="small" v-model="addressData.phone" placeholder=""></el-input>
      <el-select class="mb16" v-model="addressData.province_id" placeholder="" @change="initCity">
        <el-option :label="item.name" :value="item.id" v-for="(item,index) in areaList" :key='index'></el-option>
      </el-select>
      <el-select v-if="addressData.province_id!=''" v-model="addressData.city_id" placeholder="" @change="initRegion">
        <el-option :label="item1.name" :value="item1.id" v-for="(item1,index1) in areaList[addressData.province_id]['city']"
          :key='index1'></el-option>
      </el-select>
      <el-select v-if="addressData.city_id!=''" v-model="addressData.region_id" placeholder="">
        <el-option :label="item2.name" :value="item2.id" v-for="(item2,index2) in areaList[addressData.province_id]['city'][addressData.city_id]['region']"
          :key='index2'></el-option>
      </el-select>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogFormVisible(false)"> </el-button>
        <el-button size="small" type="primary" @click="dialogFormVisible(true)"> </el-button>
      </div>
  </el-dialog>
</template>

<script>
  import select_region from '@/utils/select.region.js';
  import OrderApi from '@/api/order.js';
  export default {
    data() {
      return {
        /**/
        dialogVisible: false,
        /**/
        type: 'error',
        /**/
        params: null,
        reverse: false,
        order_id:0,
        activities: [],
        /**/
        areaList: select_region,
        address:{
          name:'',
          phone:'',
          region:{
            province:'',
            province_id:0,
            city:'',
            city_id:0,
            region:'',
            region_id:0,
            detail:''
          }
      }
    }},
    props:['isChange','addressData'],
    watch: {
      isChange: function(n, o) {
        if (n != o) {
            this.dialogVisible = n;
            this.type = 'error';
        }
      },
    },
    mounted() {
    },
    methods: {
    /*id*/
    initCity() {
      this.addressData.city_id = ''
    },

    /*id*/
    initRegion() {
      this.addressData.region_id = ''
    },
      /**/
      dialogFormVisible(flag) {
        console.log(flag)
        if(flag){
          this.$emit('closeDialog', {
            type: this.type,
            openDialog: false,
            params: this.addressData
          });
        }else{
          this.$emit('closeDialog',false)
        }

      },
    }
  }
</script>

<style>

</style>



