<template>
  <!--
    	wangxw
    	2019-11-05
    	diy 
    -->
  <div>
    <div class="common-form">
      <span>{{ curItem.name }}</span>
    </div>
    <el-form size="small" :model="curItem" label-width="100px">
      <!---->
      <el-form-item label="">
        <div class="d-s-c">
          <el-color-picker v-model="curItem.style.background"></el-color-picker>
          <el-button type="button" style="margin-left: 10px;" @click.stop="$parent.onEditorResetColor(curItem.style, 'btnColor', '#ffffff')"></el-button>
        </div>
      </el-form-item>
      <!--  -->
      <el-form-item label="">
        <el-radio-group v-model="curItem.style.lineStyle">
          <el-radio :label="'solid'"></el-radio>
          <el-radio :label="'dashed'"></el-radio>
          <el-radio :label="'dotted'"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <div class="d-s-c">
          <el-color-picker v-model="curItem.style.lineColor"></el-color-picker>
          <el-button type="button" style="margin-left: 10px;" @click.stop="$parent.onEditorResetColor(curItem.style, 'btnColor', '#ffffff')"></el-button>
        </div>
      </el-form-item>
      <!---->
      <el-form-item label=""><el-slider v-model="curItem.style.lineHeight" show-input></el-slider></el-form-item>
      <!---->
      <el-form-item label=""><el-slider v-model="curItem.style.paddingTop" show-input></el-slider></el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      };
    },
    props: ['curItem', 'selectedIndex', 'opts'],
    created() {
      this.curItem.style.lineHeight = parseInt(this.curItem.style.lineHeight);
      this.curItem.style.paddingTop = parseInt(this.curItem.style.paddingTop);
    },
    methods: {

    }
  };
</script>

<style>

</style>



