<template>
  <!--
    	wangxw
    	2019-10-26
    	-
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false"
    :close-on-press-escape="false">
    <el-form size="small" :model="form" :rules="formRules" ref="form">
      <el-form-item label="" prop="table_no" :label-width="formLabelWidth">
        <el-input v-model="form.table_no" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="type_id" :label-width="formLabelWidth">
        <el-select v-model="form.type_id" placeholder="">
          <el-option v-for="item in type" :key="item.type_id" :label="item.type_name" :value="item.type_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="area_id" :label-width="formLabelWidth" >
        <el-select v-model="form.area_id" placeholder="">
          <el-option v-for="item in area_list" :key="item.area_id" :label="item.area_name" :value="item.area_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="sort" :label-width="formLabelWidth">
        <el-input v-model.number="form.sort" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="addUser" :loading="loading"> </el-button>
    </div>
  </el-dialog>

</template>

<script>
  import StoreApi from '@/api/store.js';
  export default {
    components: {},
    data() {
      return {
        form: {
          table_no: '',
          area_id: '',
          type_id: '',
          sort: 100,
        },
        formRules: {
          table_no: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          area_id: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          type_id: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          sort: [{
            required: true,
            message: ''
          }, {
            type: 'number',
            message: ''
          }]
        },
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false,
        loading: false,
        /**/
        isupload: false,
      };
    },
    props: ['open_add', 'addform', 'type','area_list'],
    created() {
      this.dialogVisible = this.open_add;
    },
    methods: {
      /**/
      addUser() {
        let self = this;
        let params = self.form;
        console.log(params)
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            StoreApi.addTable(params).then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.dialogFormVisible(true);
            }).catch(error => {
              self.loading = false;
            });
          }
        });
      },

      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          })
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          })
        }
      }
    }
  };
</script>

<style>
  .img {
    margin-top: 10px;
  }
</style>



