<template>
  <!--
    	luoyiming
    	2019-10-26
    	--/
    -->
  <div>
    <!---->
    <div class="common-form mt50">/</div>

    <!---->
    <el-form-item label="">
      <el-radio-group v-model="form.model.deduct_stock_type">
        <el-radio :label="10"></el-radio>
        <el-radio :label="20"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.model.special_id">
        <el-radio :label="0"></el-radio>
        <el-radio :label="item.category_id" v-for="(item,index) in form.special" :key="index">{{item.name}}</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="model.product_unit">
      <el-autocomplete class="inline-input" v-model="form.model.product_unit" placeholder=":"
        :fetch-suggestions="querySearch"></el-autocomplete>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.model.spec_type" @change="changeSpec">
        <el-radio :label="10" v-if="!form.isSpecLocked||(form.isSpecLocked&&form.model.spec_type==10)"></el-radio>
        <el-radio :label="20" v-if="!form.isSpecLocked||(form.isSpecLocked&&form.model.spec_type==20)"></el-radio>
      </el-radio-group>
      <div v-if="form.isSpecLocked" class="red"></div>
    </el-form-item>

    <!---->
    <template v-if="form.model.spec_type == 10">
      <Single></Single>
    </template>

    <!---->
    <template v-if="form.model.spec_type == 20">
      <Many></Many>
    </template>

  </div>
</template>

<script>
  import Single from './spec/Single.vue';
  import Many from './spec/Many.vue';
  export default {
    components: {
      /**/
      Single,
      /**/
      Many
    },
    data() {
      return {
        restaurants: []
      }
    },
    inject: ['form'],
    methods: {
      changeSpec(e) {
        console.log(e)
        if (e == 10) {
          this.form.sku = {
            product_price: '',
            stock_num: '',
            bag_price: '',
            cost_price: 0,
            is_full: 0
          }
        } else if (e == 20) {
          this.form.sku = [];
        }
      },
      querySearch(queryString, cb) {
        let self = this;
        if (self.restaurants.length == 0) {
          self.form.unit.forEach((item, index) => {
            self.restaurants.push({
              value: item.unit_name
            })
          })
        }

        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
    }
  };
</script>

<style>

</style>



