<template>
  <!--
    	wangxw
    	2019-10-26
    	
    -->
  <div class="product">
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" @click="addClick" icon="el-icon-plus" v-auth="'/store/table/area/add'"></el-button>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" row-key="category_id"  style="width: 100%" v-loading="loading">
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="area_name" label="" width="180"></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="100">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small"
                v-auth="'/store/table/table/edit'"></el-button>
                <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/store/table/area/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <Add v-if="open_add" :open_add="open_add" :addform="categoryModel" @closeDialog="closeDialogFunc($event, 'add')">
    </Add>
    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :editform="categoryModel"
      @closeDialog="closeDialogFunc($event, 'edit')"></Edit>

  </div>
</template>

<script>
  import StoreApi from '@/api/store.js';
  import Add from './add.vue';
  import Edit from './edit.vue';
  export default {
    components: {
      Add,
      Edit
    },
    data() {
      return {
        /**/
        loading: true,
        activeName: 'first',
        /**/
        tableData: [],
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        categoryModel: {
          model:''
        },
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      handleClick() {
        this.getData();
      },
      /**/
      getData() {
        let self = this;
        self.loading = true;
          StoreApi.arealist({}, true)
            .then(data => {
              self.loading = false;
              self.tableData = data.data.list.data;
              self.categoryModel = data.data.list.data;
            })
            .catch(error => {
              self.loading = false;
            });
      },
      /**/
      addClick() {
        this.open_add = true;
      },

      /**/
      editClick(item) {
        this.categoryModel.model = item;
        this.open_edit = true;
      },

      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getData();
          }
        }
      },
      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          StoreApi.deleteArea({
            area_id: row.area_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
      },
    }
  };
</script>

<style></style>



