<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="">
        <div>
          <el-radio v-model="form.radio" label="local" @change="getRadio"></el-radio>
        </div>
      </el-form-item>
      <div v-if="form.radio == 'local'">
        <el-form-item label="()">
          <el-input v-model="form.engine.local.time" class="max-w460"></el-input>
          <div class="tips">0</div>
        </el-form-item>
      </div>
      <!---->
      <div class="common-button-wrapper">
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          radio: 'local',
          engine: {
            local: {
              time: '',
            },
          }
        },
        loading: false
      };
    },
    created() {
      this.getData();
    },

    methods: {
      getData() {
        let self = this;
        SettingApi.deliverDetail({}, true)
          .then(data => {
            let vars = data.data.vars.values;
            self.form.radio = vars.default;
            self.form.engine.local = vars.engine.local;
          })
          .catch(error => {});
      },
      //
      onSubmit() {
        let self = this;
        self.loading = true;
        let params = this.form;
        SettingApi.editDeliver(params, true)
          .then(data => {
            self.loading = false;
            self.$message({
              message: '',
              type: 'success'
            });
          })
          .catch(error => {
            self.loading = false;
          });
      },
      //
      getRadio(val) {}
    }
  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



